<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data:{},
  methods:{
    checkLogin(){
      if(!this.getCookie("JSESSIONID")){
          this.$router.push("/login");
      }
    }
  }
}
</script>

<style>
  html{
    width: 100%;
    min-height: 1200px;
    font:14px/1.5 "PingFang SC","微软雅黑","Microsoft YaHei",Helvetica,"Helvetica Neue",Tahoma,Arial,sans-serif;
  }
  body{
    width: 100%;
    height: 100%;
    min-height: 1200px;
    margin: 0;
    background: #e6e6d5;
    color: #696a52;
  }
 #app {
  height: 100%;
  min-height: 1200px;
  width: 80%;
  margin: 0 10%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background: #fff;
  color: #2c3e50;
  
 }
</style>



