// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
//import router from './router'
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';

Vue.config.productionTip = false

const index=resolve=>require(['./components/index/index.vue'],resolve);
const content=resolve=>require(['./components/content/content.vue'],resolve);
const sale=resolve=>require(['./components/sale/sale.vue'],resolve);
const saleList=resolve=>require(['./components/saleList/saleList.vue'],resolve);
const releaseSale=resolve=>require(['./components/releaseSale/releaseSale.vue'],resolve);
const loginMenu=resolve=>require(['./components/index/loginMenu/loginMenu.vue'],resolve);
const good=resolve=>require(['./components/good/good.vue'],resolve);
Vue.use(VueRouter);
Vue.use(VueResource);

const routes = [
  {path:"/", component: index},
  {path:"/sale", component: sale,
	children:[{
		path:"", component:saleList
	},
  {
    path:"/sale/good", component:good
  },
	{
		path:"/sale/releaseSale", component:releaseSale
	}
	]},
  {path:"/buy", component: content},
  {path:"/login",component: loginMenu}
]
 

const router = new VueRouter({
  routes 
})

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



